/* Existing custom styles preserved where necessary */
/* Auth page styles */
.auth-page {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: calc(100vh - 72px);
  background: linear-gradient(180deg, #f8fafc 0%, #ffffff 100%);
  padding: 24px;
}
.auth-card {
  width: 420px;
  background: white;
  border-radius: 12px;
  padding: 28px;
  box-shadow: 0 8px 24px rgba(11,92,255,0.06);
  border: 1px solid rgba(15,23,42,0.04);
}
.auth-logo {
  width: 64px;
  height: 64px;
  border-radius: 12px;
  background: linear-gradient(135deg,#0b5cff,#6ec1ff);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: 700;
  font-size: 20px;
  margin-bottom: 12px;
}
.auth-title {
  font-size: 28px;
  margin: 0 0 8px 0;
  color: #0f172a;
}
.auth-sub {
  margin: 0 0 18px 0;
  color: #6b7280;
  font-size: 14px;
}
.form-label {
  display: block;
  font-weight: 600;
  color: #374151;
  margin-bottom: 6px;
  font-size: 13px;
}
.form-input {
  width: 100%;
  padding: 10px 12px;
  border-radius: 8px;
  border: 1px solid #e6edf8;
  box-sizing: border-box;
  outline: none;
  font-size: 14px;
  background: #ffffff;
}
.form-row { margin-bottom: 12px; }
.auth-actions { display: flex; align-items: center; justify-content: space-between; gap: 12px; margin-top: 12px; }
.btn.full { width: 100%; }
.error { color: #b91c1c; margin-top: 8px; font-size: 13px; }
.muted-link { color: #6b7280; font-size: 13px; text-decoration: none; }

/* Admin stat cards */
.stat-card { display: flex; flex-direction: column; gap: 6px; min-height: 140px; justify-content: center; }
.stat-value { font-size: 28px; font-weight: 700; color: #0b5cff; }
.nav-signout { background: white; color: #0b5cff; padding: 6px 10px; border-radius: 8px; text-decoration: none; }
.nav-export { background: white; color: #0b5cff; padding: 6px 10px; border-radius: 8px; text-decoration: none; display: none; }
.btn.nav-signout { background: #0f172a; color: #fff; padding: 8px 12px; border-radius: 10px; text-decoration: none; font-weight: 700; }
.btn.nav-signout:hover { opacity: 0.95; transform: translateY(-1px); }
.nav-link { padding: 8px 10px; border-radius: 8px; }
.nav-link:hover { background: rgba(15,23,42,0.02); }

/* Export page */
.export-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 12px;
}
.export-tile {
  padding: 14px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 120px;
  transition: transform 160ms ease, box-shadow 160ms ease;
}
.export-tile:hover {
  transform: translateY(-6px);
  box-shadow: 0 10px 30px rgba(11,92,255,0.08);
}
.export-tile .small { color: #6b7280; }

/* Site header styles */
.site-header {
  background: linear-gradient(180deg,#fff8ee 0%, #fff6e6 100%);
  color: #0f172a;
  padding: 18px 20px;
  margin-bottom: 26px;
  box-shadow: 0 6px 18px rgba(15,23,42,0.05);
  border-bottom: 1px solid rgba(15,23,42,0.04);
}
.site-container {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  gap: 18px;
}
.site-logo {
  height: 44px;
  width: 44px;
  border-radius: 8px;
  -o-object-fit: cover;
     object-fit: cover;
  background: #eef2ff;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  color: var(--brand);
  padding: 6px;
}
.site-logo.placeholder { display: inline-flex; }
.site-title {
  font-weight: 800;
  font-size: 18px;
  color: #0f172a;
  margin-left: 4px;
}
.site-nav {
  margin-left: auto;
  display: flex;
  align-items: center;
  gap: 16px;
}
.site-nav .nav-link {
  color: #0f172a;
  text-decoration: none;
  font-weight: 600;
  padding: 10px 14px;
  border-radius: 10px;
  transition: background 140ms ease, transform 120ms ease, color 120ms;
}
.site-nav .nav-link:hover {
  background: rgba(99,102,241,0.08);
  color: var(--brand);
  transform: translateY(-2px);
}
.site-nav .nav-link:active { transform: translateY(0); }
.site-nav .nav-signout {
  background: #0f172a;
  color: #fff;
  padding: 8px 14px;
  border-radius: 12px;
  font-weight: 700;
  text-decoration: none;
}
.site-nav .nav-signout:hover { transform: translateY(-2px); opacity: 0.98; }

@media (max-width: 720px) {
  .site-container { padding: 0 12px; }
  .site-title { display: none; }
  .site-nav .nav-link { font-size: 14px; padding: 6px 8px; }
  .site-logo { height: 36px; width: 36px; }
}

/* Admin dashboard styles */
.page-header h1 {
  font-size: 36px;
  margin: 10px 0 6px 0;
}
.page-header .small { margin-top: 0; color: #6b7280; }
.stats-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 18px;
  margin-top: 18px;
}
.stat-card {
  padding: 20px;
  min-height: 140px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.stat-top .small { color: #6b7280; }
.welcome-card { padding: 22px; }

@media (max-width: 980px) {
  .stats-grid { grid-template-columns: repeat(2, 1fr); }
}
@media (max-width: 680px) {
  .stats-grid { grid-template-columns: 1fr; }
  .page-header h1 { font-size: 28px; }
}
